<script>
import CarouselPages from '../components/CarouselPages.vue'
import FooterPages from '@/components/FooterPages.vue'

export default {
  name: 'HomeView',
  components: {
    CarouselPages,
    FooterPages
  },
  mounted() {
    const threshold = .4;
    const options = {
        root: null,
        rootMargin: '0px',
        threshold
    }

    const handleIntersect = function (entries) {
      entries.forEach(function (entry) {
        if (entry.intersectionRatio > threshold) {
          entry.target.classList.add('reveal-visibility')
        } /* else {
          entry.target.classList.remove('reveal-visibility')
        } */
      })
    }

    const observer = new IntersectionObserver(handleIntersect,options);
    document.querySelectorAll('[class*="reveal"]').forEach(function(r) {
      observer.observe(r)
    })
  }
}
</script>

